.we-are-all-main {
    display: grid;
    grid-template-columns: 50% 50%;
    background-image: url('../images/webp/weAreAllBg_edited.webp');
    min-height: 500px;

    .we-are-all-left {
        grid-column: 1/2;

        h3 {
            font-size: 50px;
            font-weight: 400;
            line-height: 72px;
        }

        p {
            font-size: 14px;
            font-weight: 300;
            line-height: 22px;
            margin-top: 30px;
        }
    }

    .we-are-all-right-one {
        grid-column: 2/3;
        line-height: 30px;
        font-size: 16px;
        font-weight: 300;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;

        .cytat{
            width: fit-content;
            height: fit-content;

            .cudzyslow-ico {
                display: inline;
                padding-right: 10px;
            }
    
            .cudzyslow-text {
                display: inline;
            }
        }


        .the-boss {
            width: fit-content;
            height: fit-content;
            display: grid;
            grid-template-columns: 1fr 1fr;
    
            .boss-foto {
                grid-column: 1/2;
                display: flex;
                align-items: flex-end;
    
                img {
                    max-width: 100px;
                }
            }
    
            .boss-text {
                grid-column: 2/3;
                display: flex;
                align-items: flex-end;
                justify-content: flex-start;
            }
        }

    }
    .we-are-all-right-two {
        grid-column: 2/3;

    }


}