.main-container{
    width: 100%;
    background-image: url('../images/webp/diamond_edited.webp');
    background-size: cover;
    background-position: no-repeat; 
    display: flex;
    justify-content: center;

    .content-container{
        max-width: 1636px;
        min-width: 85%;
        padding: 157px 0px 105px 0px;

        .small-text{
            font-size: 14px;
            font-weight: 300;
            line-height: 20px;
        }

        .first-title{
            margin-top: 33px;
            font-size: 50px;
            font-weight: 400;
            line-height: 72px;
        }

        .counters{
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            gap: 50px;
            margin-top: 149px;

            .counter1{
                grid-column: 1/2;

            }
            .counter2{
                grid-column: 2/3;
            }
            .counter3{
                grid-column: 3/4;
            }
            .counter4{
                grid-column: 4/5;
            }

            .thin-line{
                size: 0.6px;
                color: #ffffff;
                margin-bottom: 30px;
            }

            .counter-description{
                margin-top: 24px;
                font-size: 13px;
                line-height: 17px;
                font-weight: 300;
                color: #DCDBDB;
            }
        }


    }
}